const delay = ms => {
  return new Promise(resolve =>
    setTimeout(() => {
      resolve(ms);
    }, ms),
  );
  // Ð¢Ð²Ð¾Ð¹ ÐºÐ¾Ð´
};

// '%c Ð—Ð°ÐµÐ·Ð´ Ð¾ÐºÐ¾Ð½Ñ‡ÐµÐ½, Ð¿Ñ€Ð¸Ð½Ð¸Ð¼Ð°ÑŽÑ‚ÑÑ ÑÑ‚Ð°Ð²ÐºÐ¸.',
